ction_text {\n    font-family: 'SFProText-Regular';\n    font-size: 10.5pt;\n    color: rgb(7, 20, 45);\n}\n\n#company_name {\n    font-family: 'SFProText-Bold';\n    font-size: 10.5pt;\n    color: rgb(7, 20, 45);\n}\n\n.settings_link {\n    width: 18pt;\n    height: 18pt;\n    background-repeat: no-repeat;\n    background-size: 18pt 18pt;\n    position: relative;\n    bottom: 10pt;\n    right: 10pt;\n    float: right;\n\n    background-image: url(../../img/settings-icon_light.svg);\n}\n\n.settings_link:hover {\n    background-image: url(../../img/settings-icon-hover_light.svg);\n}\n\n#back_arrow {\n    position: relative;\n    /* z-index: 999999999999999999999; */\n    left: 18pt;\n    top: 32pt;\n}\n  \n.back_arrow {\n    background-repeat: no-repeat;\n    background-size: 8pt 12pt;\n    width: 8pt;\n    height: 12pt;\n    position: relative;\n    z-index: 999999999;\n    background-image: url(../../img/back_arrow_light.svg);\n    /* position: relative;\n    top: 21pt;\n    left: 18.75pt; */\n}\n\n.feedback_title {\n    font-family: 'SFProText-Regular';\n    font-size: 18pt;\n    font-weight: bold;\n    position: relative;\n    top: 15pt;\n    margin-left: auto;\n    margin-right: auto;\n    text-align: center;\n    color: rgb(7, 20, 45);\n}\n\n.BDTLL_feedback_outer {\n    background-image: url(../../img/feedback_o_layer.png);\n    display:block;\n    position:fixed;\n    width:100%;\n    height:100%;\n    z-index:9999999999;\n    left:0;\n    top:0;\n}\n\n.BDTLL_feedback_container {\n    width: 300pt;\n    font-family: \"SFProText-Regular\";\n    text-align: left;\n}\n\n.BDTLL_feedback_header {\n    background-image: url(../../img/bitdefender_logo.svg);\n    height: 39px;\n    background-color: #252628;\n    border-radius: 3px 2px 0 0;\n    margin: 0;\n    line-height:39px;\n    color: #bbc7d4;\n    font-size: 14px;\n    background-size: 85px 14px;\n    background-position: 20px 12px;\n    background-repeat: no-repeat;\n    padding-left: 110px;\n    position: relative;\n}\n\n.BDTLL_feedback_close {\n    background-image: url(../../img/close.svg);\n    width: 39px;\n    height: 39px;\n    position: absolute;\n    top: 0;\n    right: 0;\n    background-size: 15px 15px;\n    background-repeat: no-repeat;\n    background-position: center center;\n    cursor: pointer;\n}\n\n.BDTLL_feedback_content {\n    width: 350px;\n    height: 448px; /*auto*/\n    margin: 30pt auto 0pt auto;\n}\n\n.BDTLL_email_title {\n    font-family: 'SFProText-Regular';\n    font-size: 10.5pt;\n    color: rgb(81, 91, 109);\n}\n\n.BDTLL_description_title {\n    font-family: 'SFProText-Regular';\n    font-size: 10.5pt;\n    color: rgb(81, 91, 109);\n}\n\n.BDTLL_feedback_heading {\n    font-size: 24px;\n    text-align:center;\n    color: #252628;\n}\n\n.BDTLL_invalid_input_email {\n    font-size: 9pt;\n    color: rgb(236, 28, 36);\n    margin-top: 2pt;\n    margin-bottom: 5.5pt;\n}\n\n.BDTLL_feedback_input {\n    font-family: 'SFProText-Regular';\n    font-size: 10.5pt;\n    line-height: 16pt;\n    height: 24pt;\n    margin: 9pt 0pt 18pt 0pt;\n    padding: 0 11pt;\n    border-radius: 3pt;\n    width: 318px;\n    color: rgb(7, 20, 45);\n    background-color: white;\n    border: 1pt solid rgb(158, 159, 164);\n}\n\n.BDTLL_invalid_feedback_message {\n    font-size: 9pt;\n    color: rgb(236, 28, 36);\n    margin-top: 2px;\n    margin-bottom: 4px;\n}\n\n.BDTLL_feedback_textarea {\n    font-family: 'SFProText-Regular';\n    font-size: 10.5pt;\n    line-height: 16pt;\n    border-radius: 3pt;\n    padding: 12pt 10pt;\n    width: 320px;\n    height: 133px;\n    margin-top: 9pt;\n    margin-bottom: 15pt;\n    resize: none;\n    color: rgb(7, 20, 45);\n    background-color: white;\n    border: 1pt solid rgb(158, 159, 164);\n}\n\n.BDTLL_feedback_send {\n    height: 24pt;\n    border-radius: 3px;\n    color: #fff;\n    font-size: 10.5pt;\n    line-height: 24pt;\n    text-align: center;\n    margin: 30px 0px 0px 216px;\n    cursor: pointer;\n    width: 100pt;\n    background-color: rgb(0, 94, 255);\n}\n\n.BDTLL_feedback_link {\n    display: inline-block;\n    font-family: 'SFProText-Regular';\n    font-size: 9pt;\n    border-radius: 3pt;\n    padding: 7pt 7pt;\n    width: 333px;\n    height: 15pt;\n    overflow: hidden;\n    color: rgb(81, 91, 109);\n    background-color: rgb(237, 237, 238);\n}\n\n.BDTLL_check_include_link_containter {\n    margin-top: 3pt;\n}\n\n.BDTLL_feedback_check {\n    visibility:collapse;\n    position:absolute;\n}\n\n.BDTLL_feedback_label {\n    line-height: 20px;\n    vertical-align: top;\n    display: inline-block;\n    font-size: 10.5pt;\n    padding-left: 30px;\n    background-repeat: no-repeat;\n    background-size: 20px 20px;\n    background-position: 0 center;\n    user-select: none;\n    cursor: pointer;\n    color: rgb(64, 64, 64);\n}\n\n.BDTLL_submit_result_ok {\n    position: absolute;\n    bottom: 14pt;\n    left: 335px;\n    background-repeat: no-repeat;\n    background-size: 36px 26px;\n    width: 36px;\n    height: 26px;\n    z-index: 999999999;\n    background-image: url(../../img/done_light.svg);\n}\n\n.BDTLL_submit_result_failed {\n    position: relative;\n    bottom: 19pt;\n    font-family: 'SFProText-Regular';\n    font-size: 10pt;\n    color: red;\n}\n\n.hide {\n    visibility: hidden;\n    opacity: 0;\n    transition: visibility 0s 1.5s, opacity 1.5s linear;\n}\n\n.check-off {\n    background-image: url(../../img/check-off_light.svg);\n}\n\n.check-on {\n    background-image: url(../../img/check-on_light.svg);\n}\n\n.loader {\n    position: absolute;\n    bottom: 12pt; /*20pt*/\n    left: 257pt; /*152pt*/\n    background-repeat: no-repeat;\n    background-size: 32px 32px;\n    width: 32px;\n    height: 32px;\n    visibility: hidden;\n    animation: spin 1s linear infinite;\n    background-image: url(../../img/loader_light.svg);\n}\n\n.animate-in {\n    -webkit-animation: fadeIn .5s ease-in;\n    animation: fadeIn .5s ease-in;\n  }\n  \n.animate-out {\n    -webkit-transition: opacity .5s;\n    transition: opacity .5s;\n    opacity: 0;\n}\n\n@-webkit-keyframes fadeIn {\n    from { opacity: 0; }\n    to { opacity: 1; }\n}\n\n@keyframes fadeIn {\n    from { opacity: 0; }\n    to { opacity: 1; }\n}\n\n@keyframes spin {\n    0% {\n        transform: rotate(0deg);\n    }\n    100% {\n        transform: rotate(360deg);\n    }\n}\n\n@-moz-document url(\"about:blank\") {\n\thtml {\n\t  background-color: rgb(40,40,40);\n\t}\n}\n\n@-moz-document url(about:newtab) {\n\tbody, #newtab-customize-overlay {\n       background-color: rgb(40,40,40) !important;\n       color: rgb(166,166,166) !important;\n    }\n}\n\n@media (prefers-color-scheme: dark) {\n    html, body {\n      background-color: rgb(7, 20, 45);\n    }\n\n    .main {\n      background-color: rgb(7, 20, 45);\n    }\n  \n    .container_top {\n      background-color: rgb(56, 67, 87);\n    }\n  \n    #web_protection_text, #company_name {\n      color: rgb(239, 241, 249);\n    }\n  \n    .settings_link {\n      background-image: url(../../img/settings-icon-dark.svg);\n    }\n\n    .settings_link:hover {\n        background-image: url(../../img/settings-icon-hover_dark.svg);\n    }\n\n    .back_arrow {\n        background-image: url(../../img/back_arrow_dark.svg);\n    }\n\n    .feedback_title {\n        color: rgb(255, 255, 255);\n    }\n\n    .BDTLL_email_title, .BDTLL_description_title {\n        color: rgb(217, 217, 217);\n    }\n\n    .BDTLL_feedback_input, .BDTLL_feedback_textarea {\n        color: rgb(255, 255, 255);\n        background-color: rgba(56, 67, 87, 0.1);\n        border: 1pt solid rgb(81, 91, 109);\n    }\n\n    .BDTLL_feedback_link {\n        color: rgb(255, 255, 255);\n        background-color: rgba(81, 91, 87, 0.11);\n    }\n\n    .BDTLL_feedback_label {\n        color: rgb(239, 241, 249);\n    }\n\n    .BDTLL_feedback_send {\n        background-color: rgb(45, 123, 254);\n    }\n\n    .BDTLL_submit_result_ok {\n        background-image: url(../../img/done_dark.svg);\n    }\n\n    .check-off {\n        background-image: url(../../img/check-off_dark.svg);\n    }\n    \n    .check-on {\n        background-image: url(../../img/check-on_dark.svg);\n    }\n\n    .loader {\n        background-image: url(../../img/loader_dark.svg);\n    }\n  }\n"],"sourceRoot":""}]);
