import * as React from "react";
import * as ReactDOM from "react-dom";
import * as BDTLL from "../../BDTLL_common";
import "./feedback.css";
import { ErrorBoundary } from "../components/errorBoundary";
import Header from "../components/header";

interface IFeedbackProps {
    url: string;
    show: boolean;
    includeLink: boolean;
}

interface IFeedbackState {
    show: boolean;
    includeLink: boolean;
    email: string;
    message: string;
}

export class Feedback extends React.Component<IFeedbackProps, IFeedbackState> {
    private emailRef: React.RefObject<HTMLInputElement>
    private messageRef: React.RefObject<HTMLTextAreaElement>
    private validEmail: boolean;
    private validMessage: boolean;
    private submitResult: number; // 0: not sent, 1: success, -1: failed
    private submitPressed: boolean;

    // Validates email addresses which contains letters from other alphabets, but only for the part before the '@'
    private readonly EMAIL_REGEXP1 = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

    // Validates email addresses which contains letters from other alphabets after the '@'
    private readonly EMAIL_REGEXP2 = /^(?!\.)((?!.*\.{2})[a-zA-Z0-9\u0080-\u00FF\u0100-\u017F\u0180-\u024F\u0250-\u02AF\u0300-\u036F\u0370-\u03FF\u0400-\u04FF\u0500-\u052F\u0530-\u058F\u0590-\u05FF\u0600-\u06FF\u0700-\u074F\u0750-\u077F\u0780-\u07BF\u07C0-\u07FF\u0900-\u097F\u0980-\u09FF\u0A00-\u0A7F\u0A80-\u0AFF\u0B00-\u0B7F\u0B80-\u0BFF\u0C00-\u0C7F\u0C80-\u0CFF\u0D00-\u0D7F\u0D80-\u0DFF\u0E00-\u0E7F\u0E80-\u0EFF\u0F00-\u0FFF\u1000-\u109F\u10A0-\u10FF\u1100-\u11FF\u1200-\u137F\u1380-\u139F\u13A0-\u13FF\u1400-\u167F\u1680-\u169F\u16A0-\u16FF\u1700-\u171F\u1720-\u173F\u1740-\u175F\u1760-\u177F\u1780-\u17FF\u1800-\u18AF\u1900-\u194F\u1950-\u197F\u1980-\u19DF\u19E0-\u19FF\u1A00-\u1A1F\u1B00-\u1B7F\u1D00-\u1D7F\u1D80-\u1DBF\u1DC0-\u1DFF\u1E00-\u1EFF\u1F00-\u1FFFu20D0-\u20FF\u2100-\u214F\u2C00-\u2C5F\u2C60-\u2C7F\u2C80-\u2CFF\u2D00-\u2D2F\u2D30-\u2D7F\u2D80-\u2DDF\u2F00-\u2FDF\u2FF0-\u2FFF\u3040-\u309F\u30A0-\u30FF\u3100-\u312F\u3130-\u318F\u3190-\u319F\u31C0-\u31EF\u31F0-\u31FF\u3200-\u32FF\u3300-\u33FF\u3400-\u4DBF\u4DC0-\u4DFF\u4E00-\u9FFF\uA000-\uA48F\uA490-\uA4CF\uA700-\uA71F\uA800-\uA82F\uA840-\uA87F\uAC00-\uD7AF\uF900-\uFAFF\.!#$%&'*+-/=?^_`{|}~\-\d]+)@(?!\.)([a-zA-Z0-9\u0080-\u00FF\u0100-\u017F\u0180-\u024F\u0250-\u02AF\u0300-\u036F\u0370-\u03FF\u0400-\u04FF\u0500-\u052F\u0530-\u058F\u0590-\u05FF\u0600-\u06FF\u0700-\u074F\u0750-\u077F\u0780-\u07BF\u07C0-\u07FF\u0900-\u097F\u0980-\u09FF\u0A00-\u0A7F\u0A80-\u0AFF\u0B00-\u0B7F\u0B80-\u0BFF\u0C00-\u0C7F\u0C80-\u0CFF\u0D00-\u0D7F\u0D80-\u0DFF\u0E00-\u0E7F\u0E80-\u0EFF\u0F00-\u0FFF\u1000-\u109F\u10A0-\u10FF\u1100-\u11FF\u1200-\u137F\u1380-\u139F\u13A0-\u13FF\u1400-\u167F\u1680-\u169F\u16A0-\u16FF\u1700-\u171F\u1720-\u173F\u1740-\u175F\u1760-\u177F\u1780-\u17FF\u1800-\u18AF\u1900-\u194F\u1950-\u197F\u1980-\u19DF\u19E0-\u19FF\u1A00-\u1A1F\u1B00-\u1B7F\u1D00-\u1D7F\u1D80-\u1DBF\u1DC0-\u1DFF\u1E00-\u1EFF\u1F00-\u1FFF\u20D0-\u20FF\u2100-\u214F\u2C00-\u2C5F\u2C60-\u2C7F\u2C80-\u2CFF\u2D00-\u2D2F\u2D30-\u2D7F\u2D80-\u2DDF\u2F00-\u2FDF\u2FF0-\u2FFF\u3040-\u309F\u30A0-\u30FF\u3100-\u312F\u3130-\u318F\u3190-\u319F\u31C0-\u31EF\u31F0-\u31FF\u3200-\u32FF\u3300-\u33FF\u3400-\u4DBF\u4DC0-\u4DFF\u4E00-\u9FFF\uA000-\uA48F\uA490-\uA4CF\uA700-\uA71F\uA800-\uA82F\uA840-\uA87F\uAC00-\uD7AF\uF900-\uFAFF\-\.\d]+)((\.([a-zA-Z\u0080-\u00FF\u0100-\u017F\u0180-\u024F\u0250-\u02AF\u0300-\u036F\u0370-\u03FF\u0400-\u04FF\u0500-\u052F\u0530-\u058F\u0590-\u05FF\u0600-\u06FF\u0700-\u074F\u0750-\u077F\u0780-\u07BF\u07C0-\u07FF\u0900-\u097F\u0980-\u09FF\u0A00-\u0A7F\u0A80-\u0AFF\u0B00-\u0B7F\u0B80-\u0BFF\u0C00-\u0C7F\u0C80-\u0CFF\u0D00-\u0D7F\u0D80-\u0DFF\u0E00-\u0E7F\u0E80-\u0EFF\u0F00-\u0FFF\u1000-\u109F\u10A0-\u10FF\u1100-\u11FF\u1200-\u137F\u1380-\u139F\u13A0-\u13FF\u1400-\u167F\u1680-\u169F\u16A0-\u16FF\u1700-\u171F\u1720-\u173F\u1740-\u175F\u1760-\u177F\u1780-\u17FF\u1800-\u18AF\u1900-\u194F\u1950-\u197F\u1980-\u19DF\u19E0-\u19FF\u1A00-\u1A1F\u1B00-\u1B7F\u1D00-\u1D7F\u1D80-\u1DBF\u1DC0-\u1DFF\u1E00-\u1EFF\u1F00-\u1FFF\u20D0-\u20FF\u2100-\u214F\u2C00-\u2C5F\u2C60-\u2C7F\u2C80-\u2CFF\u2D00-\u2D2F\u2D30-\u2D7F\u2D80-\u2DDF\u2F00-\u2FDF\u2FF0-\u2FFF\u3040-\u309F\u30A0-\u30FF\u3100-\u312F\u3130-\u318F\u3190-\u319F\u31C0-\u31EF\u31F0-\u31FF\u3200-\u32FF\u3300-\u33FF\u3400-\u4DBF\u4DC0-\u4DFF\u4E00-\u9FFF\uA000-\uA48F\uA490-\uA4CF\uA700-\uA71F\uA800-\uA82F\uA840-\uA87F\uAC00-\uD7AF\uF900-\uFAFF]){2,63})+)$/i;

    constructor(props: IFeedbackProps) {
        super(props);
        this.state = {
            show: props.show,
            includeLink: props.includeLink,
            email: "",
            message: "",
        }
        this.emailRef = React.createRef<HTMLInputElement>();
        this.messageRef = React.createRef<HTMLTextAreaElement>();
        this.validEmail = true;
        this.validMessage = true;
        this.submitResult = 0;
        this.submitPressed = false;
        this.hideModal = this.hideModal.bind(this);
        this.toggleIncludeLink = this.toggleIncludeLink.bind(this);
        this.updateEmail = this.updateEmail.bind(this);
        this.updateMessage = this.updateMessage.bind(this);
        this.sendFeedback = this.sendFeedback.bind(this);
        this.listen();
    }

    toggleIncludeLink() {
        this.setState(prevState => {
            return {
                includeLink: !prevState.includeLink
            }
        });
    }

    showModal() {
        this.setState({ show: true });
    }

    hideModal() {
        this.setState({ show: false });
    }

    validateMessage(message: string): boolean {
        if (message.length < 5) {
            return false;
        }

        let letterCount = message.replace(/\s+/g, '').length;
        if (letterCount < 5) {
            return false;
        }

        return true;
    }

    updateEmail(evt: React.FormEvent<HTMLInputElement>) {
        let feedback_input_email = (document.getElementsByClassName("BDTLL_feedback_input") as HTMLCollectionOf<HTMLElement>)[0];
        feedback_input_email.style.borderColor = '';
        feedback_input_email.style.margin = '9pt 0pt 18pt 0pt';   

        this.validEmail = true;
        this.setState({
            email: evt.currentTarget.value
        });
    }

    updateMessage(evt: React.FormEvent<HTMLTextAreaElement>) {
        let feedback_input_message = (document.getElementsByClassName("BDTLL_feedback_textarea") as HTMLCollectionOf<HTMLElement>)[0];
        feedback_input_message.style.borderColor = '';
        feedback_input_message.style.marginBottom = '15pt';

        this.validMessage = true;
        this.setState({
            message: evt.currentTarget.value
        });
    }

    sendFeedback() {
        let feedback_input_email = (document.getElementsByClassName("BDTLL_feedback_input") as HTMLCollectionOf<HTMLElement>)[0];
        let feedback_input_message = (document.getElementsByClassName("BDTLL_feedback_textarea") as HTMLCollectionOf<HTMLElement>)[0];
        let feedback_submit_button = (document.getElementsByClassName("BDTLL_feedback_send") as HTMLCollectionOf<HTMLElement>)[0];
        let loader = (document.getElementsByClassName("loader") as HTMLCollectionOf<HTMLElement>)[0];

        // Validate email
        if (this.state.email == "" || (!this.EMAIL_REGEXP1.test(this.state.email) && !this.EMAIL_REGEXP2.test(this.state.email))) {
            this.validEmail = false;
            feedback_input_email.style.borderColor = 'rgb(236, 28, 36)';
            feedback_input_email.style.margin = '9pt 0pt 0pt';
        } else {
            this.validEmail = true;
            feedback_input_email.style.borderColor = '';
            feedback_input_email.style.margin = '9pt 0pt 18pt 0pt';   
        }

        // Validate message
        if (!this.validateMessage(this.state.message)) {
            this.validMessage = false;
            feedback_input_message.style.borderColor = 'rgb(236, 28, 36)';
            feedback_input_message.style.marginBottom = '0pt';
        } else {
            this.validMessage = true;
            feedback_input_message.style.borderColor = '';
            feedback_input_message.style.marginBottom = '15pt';
        }

        this.submitResult = 0;

        this.setState({
            show: true,
            includeLink: this.state.includeLink,
            email: this.state.email,
            message: this.state.message
        });

        if (!this.validEmail || !this.validMessage) return;

        this.submitPressed = true;
        loader.style.visibility = 'visible';
        loader.style.display = 'block';
        feedback_submit_button.style.display = 'none';

        var xhr = new XMLHttpRequest();

        xhr.open("POST", BDTLL.Consts.FEEDBACK_URL);
        xhr.setRequestHeader("content", "application/json");
        xhr.setRequestHeader("content-type", "application/json");
        xhr.setRequestHeader("x-nimbus-clientid", BDTLL.BrowserConsts.BROWSER_CLIENTID || "");

        xhr.onreadystatechange = () => {
            if (4 === xhr.readyState) {
                if (200 == xhr.status ) {
                    // ok
                    this.submitResult = 1;
                    this.submitPressed = false;
                    this.setState({
                        show: true,
                        includeLink: this.props.includeLink,
                        email: "",
                        message: ""
                    });

                    loader.style.display = 'none';
                    setTimeout(() => {
                        let resultMessage = (document.getElementsByClassName("BDTLL_submit_result_ok") as HTMLCollectionOf<HTMLElement>)[0];
                        resultMessage.classList.add("hide");
                        setTimeout(() => {
                            resultMessage.style.display = 'none';
                        }, 1500);
                    }, 100);

                    setTimeout(() => {
                        feedback_submit_button.style.display = 'block';
                    }, 1500);
                } else {
                    // failed
                    this.submitResult = -1;
                    this.submitPressed = false;
                    feedback_submit_button.style.display = 'block';

                    this.setState({
                        show: true,
                        includeLink: this.state.includeLink,
                        email: this.state.email,
                        message: this.state.message
                    });

                    loader.style.display = 'none';
                    setTimeout(() => {
                        let resultMessage = (document.getElementsByClassName("BDTLL_submit_result_failed") as HTMLCollectionOf<HTMLElement>)[0];
                        resultMessage.classList.add("hide");
                        setTimeout(() => {
                            resultMessage.style.display = 'none';
                        }, 1500);
                    }, 1500);
                }
            }
        };

        xhr.send(JSON.stringify({
            "agent": navigator.userAgent,
            "email": this.state.email,
            "lang": BDTLL.Utils.getLanguage(),
            "message": this.state.message,
            "product": BDTLL.Consts.PRODUCT_NAME,
            "url": this.state.includeLink ? this.props.url : "[NO-URL]",
            "version": BDTLL.Consts.VERSION
        }));
    }

    listen() {
        BDTLL.MessageService.addListener(
            (message: any, sender: any) => {
                if (message.command == BDTLL.Command.OPEN_FEEDBACK) {
                    console.log("Open feedback Command received.");
                    this.showModal();
                }
            }
        );
    }

    render() {
        return (
            <div>
                <div className="BDTLL_feedback_container animate-in">
                    <div className="BDTLL_feedback_content">
                        <div className="BDTLL_email_title">{BDTLL.Utils.get_localized_text("feedback_mail")}</div>
                        <input id="BDTLL_email" type="text" className="BDTLL_feedback_input"
                                value={this.state.email} onChange={this.updateEmail} ref={this.emailRef}/>
                        {!this.validEmail ? <div className="BDTLL_invalid_input_email">{BDTLL.Utils.get_localized_text("invalid_email_text")}</div> : null}
                        <div className="BDTLL_description_title">{BDTLL.Utils.get_localized_text("feedback_description_title")}</div>
                        <textarea id="BDTLL_comment_area" className="BDTLL_feedback_textarea" placeholder={BDTLL.Utils.get_localized_text("feedback_message")}
                                value={this.state.message} onChange={this.updateMessage} ref={this.messageRef}>
                        </textarea>
                        {!this.validMessage ? <div className="BDTLL_invalid_feedback_message">{BDTLL.Utils.get_localized_text("invalid_feedback_message_text")}</div> : null}
                        <div className="BDTLL_feedback_link_container">
                            <span className="BDTLL_feedback_link">{this.props.url}</span>
                        </div>
                        <div className="BDTLL_check_include_link_containter">
                            <input className="BDTLL_feedback_check" type="checkbox" id="BDTLL_include_website" onClick={this.toggleIncludeLink}/>
                            <label htmlFor="BDTLL_include_website" className={"BDTLL_feedback_label "+(this.state.includeLink ? "check-on" : "check-off")}>{BDTLL.Utils.get_localized_text("checkbox_link_feedback")}</label>
                        </div>
                        <div className="BDTLL_feedback_send" onClick={this.sendFeedback}>{BDTLL.Utils.get_localized_text("link_feedback")}</div>
                        <div className="loader"></div>
                        {
                            this.submitResult === 1 ? 
                                <div className="BDTLL_submit_result_ok"></div>
                            : (
                                this.submitResult === -1 ? 
                                    <div className="BDTLL_submit_result_failed">{BDTLL.Utils.get_localized_text("submit_result_failed_text")}</div>
                                : null
                            )
                        }
                    </div>
                </div>
            </div>
        )
    }
}

window.addEventListener('DOMContentLoaded', async function() {
    let url = await BDTLL.Utils.getCurrentTabUrl();

    let targetElement = document.getElementById("window_title");
    if (targetElement) {
        ReactDOM.render(
            BDTLL.Utils.get_localized_text("extension_name") as any,
            targetElement
        );   
    }

    targetElement = document.getElementById("feedbackPageView");
    if (targetElement) {
        ReactDOM.render(
            <div>
                <ErrorBoundary>
                    <Header/>
                    <Feedback url={url} show={true} includeLink={true}/>
                </ErrorBoundary>
            </div>,
            targetElement
        );
    }
});
